#root {
  width: 100%;
  height: 100%;
}

#team-container {
  width: 33.33%;
  float: left;
  min-height: 100%;
}

#player-container {
  width: 33.33%;
  float: left;
  min-height: 100%;
  text-align: center;  
}

#player-info-container {
  width: 33.33%;
  float: left;
  min-height: 100%;  
  }

.team-list {
  list-style: none;
  -webkit-column-count: 2;
          column-count: 2;
}

.team-logo {
  width: 60%;
  display: inline-block;
  cursor: pointer;
}

.players-list {
  display: inline-block;
  cursor: pointer;  
  list-style: none;   
}

.player-from-list {
  font-size: 170%;
  font: "Century Gothic", Futura, sans-serif; 
  text-align: center;
  font-weight: 500;
  border: 1px solid black;
  border-radius: 8px;
  margin: 5px;
  padding: 10px;
}

.player {
  position: fixed;
  text-align: center;
}

.player-photo {
  width: 300px;
}

.player-name {
  font-size: 30pt;
  font-weight: 400; 
  text-align: center;   
}

.player-info {
  padding-top: 20px;
  padding-left: 50px;
  position: fixed;
  text-align: left;
  font-size: 18pt;
  font-weight: 500;
}

.box-header {
  padding: 15px;
  font: 180% "Century Gothic", Futura, sans-serif; 
  text-align: center; 
}

.list {
text-align: center;
cursor: pointer;
margin: 10px;
padding: 10px;
list-style: none;
font: 14px "Century Gothic", Futura, sans-serif;
border: 2px solid rgba(106, 107, 104, 0.1);
border-radius: 13px;
display: inline-block;
}




